@charset "utf-8";

.gaegu-regular {
  font-family: "Gaegu", sans-serif;
  font-weight: 400;
  font-style: normal;
}


.App {
  font-family: "Gaegu", sans-serif;
  display: flex;
  flex-direction: column;
  gap: 30px;
  max-width: 580px;
  width: 100%;
  position: relative;
  margin: 0 auto;
  margin-top: 60px;
  padding-top: 40px;
  /* outline: 1px dotted red; */
  padding-left: 10px;
  padding-right: 10px;
  box-sizing: border-box;

  border: 2px solid #808080;
  box-shadow: 5px 5px 15px rgb(200,200,200);
}

.topnote{
  width: 100%;
  height: 100px;
  background-image: url(./img/notetop.png);
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  top: -50px;
  /* outline: 1px dotted red; */
  background-position: center;
  transform: translateX(-10px);
}
.App .Header {
  margin-bottom: 8px;
  color: #1f93ff;
}
.App .Header h3 {
  font-size: 1.25rem;
}
.App .Header p {
  margin-top: 1.5625rem;
  font-size:2rem;
  font-weight: 700;
}
.App .TodoEditor h4{
  font-size: 2rem;
  font-weight: 600;
  text-align: center;
}
.App .TodoEditor .editor_wrapper {
  width: 100%;
  display: flex;
  gap: 10px;
  margin-top: 20px;
}
.App .TodoEditor .editor_wrapper input {
  flex: 1;
  font-family: "Gaegu", sans-serif;
  box-sizing: border-box;
  border: 1px solid rgb(220,220,220);
  border-radius: 5px;
  padding: 15px;
  font-size: 1.2rem;
}
.App .TodoEditor .editor_wrapper button {
  cursor: pointer;
  width: 88px; border: none;
  background-color: #1f93ff;
  color: #fff;
  font-family: "Gaegu", sans-serif;
  border-radius: 5px;
  font-size: 1.4rem;
}

.App .TodoList h4 {
  font-size: 2.3rem;
  margin-bottom: 20px;
  text-align: center;
}
.App .TodoList .listCount{
  text-align: center;
}
.App .TodoList input{
  /* text-align: center; */
  font-family: "Gaegu", sans-serif;
  font-size: 1rem;
}
.App .TodoList .searchbar {
  margin-bottom: 20px;
  width: 100%; border: none;
  border-bottom: 1px solid rgb(200,200,200);
  box-sizing: border-box;
  padding-top: 15px;
  padding-bottom: 15px;
}

.App .TodoList .searchbar:focus {
  outline: none;
  border-bottom: 1px solid #1f93ff;
}
.App .TodoList .list_wrapper {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.App .TodoList .list_wrapper .TodoItem {
  display: flex;
  align-items: center;
  gap: 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid rgb(220,220,220);
}
.App .TodoList .list_wrapper .TodoItem .checkbox_col {
  width: 20px;
}
.App .TodoList .list_wrapper .TodoItem .title_col {
  flex: 1;
}
.App .TodoList .list_wrapper .TodoItem .date_col {
  font-size: 14px; color: gray;
}
.App .TodoList .list_wrapper .TodoItem .btn_col button {
  cursor: pointer; color: gray;
  font-size: 14px; border: none;
  border-radius: 5px; padding: 5px;
}
